import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    headerTitle: 'Restoran',
    jela: [],
    kategorije: [],
    svaJelaIDs: [],
    token: '',
  },
  getters: {
  },
  mutations: {
    addKategorije(state, data) {
      state.kategorije = data;
    },
    addJela(state, data) {
      state.jela[data.id] = data;
    },
    addSvaJelaIDs(state, niz){
      state.svaJelaIDs = niz;
    },
    setToken(state, token) {
      if (!token.startsWith('token='))
        token = 'token='+token;
      state.token = token;
      localStorage.token = token;
      console.log(token);
    },
    removeToken(state) {
      state.token = '';
      localStorage.token = '';
    },

  },
  actions: {
    fetchKategorije({ commit, state }) {

      return fetch('http://localhost:9000/kategorija/', {
        method: 'GET',
           headers: {
            'Authorization': `Bearer ${state.token}`,
                'Content-Type': 'application/json' 
            },
            })
        .then((response) => response.json())
        .then((data) => {
          console.log(data);
          commit('addKategorije', data);
        })
        .catch((error) => {
          console.error('Error fetching data:', error);
        });
    },
    async getJelo({ commit, state }, jeloID) {
      return new Promise((resolve, reject) => {
        if (state.jela[jeloID]) {
          resolve(state.jela[jeloID]);
        }
        else {
          fetch(`http://localhost:9000/jelo/${jeloID}`, {
            method: 'GET',
               headers: {
                'Authorization': `Bearer ${state.token}`,
                    'Content-Type': 'application/json' 
                },
                })
            .then(res => res.json())
            .then(data => {
              console.log(data);
              commit('addJela', data)
              resolve(data);
            });
        }
      });

    },
    async fetchSvaJela({commit, state}){
      fetch("http://localhost:9000/jelo", {
        method: 'GET',
           headers: {
            'Authorization': `Bearer ${state.token}`,
                'Content-Type': 'application/json' 
            },
            })
        .then( res=>res.json() )
          .then( data=> commit('addSvaJelaIDs', data) );
    },
    register({ commit }, obj) {
      fetch('http://127.0.0.1:9001/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
      }).then( res => res.json() )
        .then( data => commit('setToken', data.token) );
    },
    login({ commit }, obj) {
      fetch('http://127.0.0.1:9001/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(obj)
    }).then( res => res.json() )
      .then( data => {
        if (data.msg) {
          alert(data.msg);
        } else {
          commit('setToken', data.token)
        }
      });
    }

  },
  modules: {
  }
})
