<template>
     <div class="jelo" @click="kliknutoJelo(jelo.id)">
      <div class="img-wrapper">
        <img class="blur" :src="`/jela/${jelo.id}.png`" :alt="`Nema slike`"/>
        <div class="content fade">
          Klikni za detalje
        </div>
      </div>
    <h3>{{ jelo.naziv }}</h3>
  </div>

</template>

<script>
export default {
  name: 'JeloSingle',  
  props: {
    jelo: Object
  },
  methods: {
    checkImageExists(filePath) {
      const img = new Image();
      img.src = filePath;
      
      return img.complete && img.naturalWidth !== 0;
    },
    kliknutoJelo(id){
        this.$router.push({ name: 'Jelo', params: { id: id } });
   }

  }
}


</script>

<style scoped>
.jelo{
    width:150px;
    display:inline-block;
    margin:10px;
    vertical-align: top;
}

.img-wrapper {
  position: relative;
  overflow: hidden;
}

.img-wrapper > img {
    display: block;
    width:150px;
    height:150px;
    aspect-ratio: 1/1;
    object-fit: cover;
    object-position: center;
}

.img-wrapper > .content {
    position: absolute;
    inset: 0;
    font-size: 2rem;
    padding: 1rem;
    background: rgba(255, 255, 255, .4);
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-wrapper > img,
.img-wrapper > .content {
    transition: 200ms ease-in-out;
}

.img-wrapper > .content.fade {
  opacity: 0;
}

.img-wrapper:hover > .content.fade {
  opacity: 1;
}

.img-wrapper:hover > img.blur {
  transform: scale(1.1);
  filter: blur(5px);
}

.jelo p{
    height:150px;
    margin:0;
    background-color:silver;
    text-align:center;
}

.jelo h3{
  font-size: 20px;
  font-weight: bold;
}
</style>

