

<template>
  
  <div>
    <img alt="Restaurant logo" src="../assets/logo.png" class="center">
    <Header v-bind:title="headerTitle"/>
    <button @click="prev()">Prethodno</button>
    <button @click="next()">Sledece</button>
    <JeloList v-if="svaJelaIDs" :jelaIDs="svaJelaIDs.slice(current*10, current*10+10)"/>
    <p v-else>Lista još nije spremna</p>
  </div>

</template>

<style>
@import url('https://fonts.googleapis.com/css2?family=Gloock&display=swap');

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

nav {
  padding: 30px;
}

nav a {
  font-weight: bold;
  color: #2c3e50;
}

nav a.router-link-exact-active {
  color: #42b983;
}

.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
</style>

<script>
import Header from '../components/Header.vue'
import JeloList from '../components/JeloList.vue'
import { mapActions, mapState } from 'vuex';


export default {
  name: 'App',
  components: {
    Header, JeloList,
  },
  data(){
    return{
      headerTitle: "Restoran",
      current: 0
    }
  },
  computed:{
    ...mapState([
      'svaJelaIDs'
    ])
  },

  methods:{
    ...mapActions([
      'fetchSvaJela'
    ]),
    next(){
      if( this.current * 10 < this.svaJelaIDs.length ){
        this.current++;
      }
    },
    prev(){
      if( this.current > 0){
        this.current--;
      }
    }

  },
  mounted(){
    this.fetchSvaJela();
  }


}


</script>
