

<template>
  
    <div>
      <img alt="Restaurant logo" src="../assets/logo.png" class="center">
      <Header v-bind:title="headerTitle"/>
      <hr>
      <div v-if="jelo">
        <div class="jelo" >
            <Header v-bind:title="jelo.naziv"/>
            <div class="info">
             
                <img class="slika"
                :src="`/jela/${jelo.id}.png`" :alt="`Nema slike`"/>
             
                 
                <div class="opis">
                    <h2>Opis</h2>
                    <p>{{ jelo.opis }}</p>
                    <h2>Cena</h2>
                    <p>{{ jelo.cena }}rsd</p>
                </div>
                
            </div>
        
          </div>
      </div>
      
      <div v-else>
          Loading...
      </div>
  </div>
  </template>
  
<style scoped>


.slika {
    width: 200px;
}

.jelo {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.info {
    display: flex;
  justify-content: center;
  text-align: center;
}

.opis {
  margin-left: 20px;
  max-width: 400px; 
}


</style>

  
  <script>
  import Header from '../components/Header.vue'
  import { mapActions, mapState } from 'vuex';
  export default {
    name: 'Jelo',
    components: {
      Header
    },
    data(){
      return{
        headerTitle: "Restoran",
        jelo: null
      }
    },
    methods: {
      ...mapActions([
      'getJelo'
    ])

    },
    mounted(){
      this.getJelo(this.$route.params.id)
      .then( res => {
        this.jelo = res;
      });

    }
  }

  </script>
  