<template>
    <div>
        <Header title="Kategorije jela"/>
        <b-pagination
          v-model="currentPage"
          :per-page="perPage"
          :total-rows="this.kategorije.length"
          aria-controls="tabelaKategorije"
        ></b-pagination>

        <b-table
          striped
          hover
          :items="this.kategorije"
          :fields="fields"    
          :per-page="perPage"
          :current-page="currentPage"
          id="tabelaKategorije">
            <template #cell(logo)="data">
              <img :src="`/kategorije/${data.item.id}.png`" />
            </template>
        </b-table>

    </div>
</template>
<script>
import Header from '@/components/Header.vue'
import { mapActions, mapState } from 'vuex';

export default {
  name: 'Kategorije',
  components: {
    Header
  },
  computed:{
    ...mapState([
      'kategorije'
    ])
  },
  methods: {
    ...mapActions([
        'fetchKategorije'
      ])
  },
  data(){
    return{
      perPage:5,
      currentPage: 1,
      fields: [
        {
          key: "naziv",
          sortable: true,
          label: "Kompanija"
        },
        {
          key: "logo",
          label:"",
        }

      ]

    }
  },
  mounted(){
    this.fetchKategorije();
  }
,

}
</script>
