<template>
    <div>
        <JeloSingle v-for="jeloItem in jela" 
			:key="jeloItem.id" :jelo="jeloItem"/>
    </div>

</template>

<script>
import JeloSingle from '@/components/JeloSingle.vue';
import { mapActions, mapState } from 'vuex';
export default {
    name: 'JeloList',
    components: {
        JeloSingle
    },
    data() {
        return {
            jela: []
        }
    },
    props: {
        jelaIDs: {
            type: Array,
        }
    },
    methods: {
      ...mapActions([
        'getJelo'
      ])

    },
    mounted() {
        
        console.log("Usao sam u mount za JeloList");
        if (this.jelaIDs) {
            this.jelaIDs.map( obj => {
                this.getJelo(obj.id).then( jelo => this.jela.push(jelo));
                console.log("jela: ");
            console.log(obj);
        });
        }
        
        

        
    },
    watch: {
        jelaIDs(nVal) {
            console.log("Nval je: " + nVal);
            this.jela = [];
            if (nVal) {
                nVal.map( obj => {
                    this.getJelo(obj.id).then( jelo => this.jela.push(jelo));
                });
                console.log("NVal: " + JSON.stringify(nVal));
            }
            
        }
    }

}
</script>

<style scoped>
.header{
    font-family: 'Gloock', serif;
    text-align: center;
}
</style>
