

<template>
  
    <div>
      <Header v-bind:title="headerTitle"/>
      <b-alert :variant="statusnaPorukaTip" :show="statusnaPoruka!=null">
            {{ statusnaPoruka }}
      </b-alert>
      <div>
            <b-container fluid style="padding: 5px; margin: 5px;">
                <b-row>
                    <b-col sm="3">
                        <label for="adresa">Adresa</label>
                    </b-col>
                    <b-col sm="9">
                        <b-form-input id="adresa" 
					:state="validnaAdresa"
v-model="forma.adresa"></b-form-input>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col sm="3">
                        <label>Telefon</label>
                    </b-col>
                    <b-col sm="9">
                        <b-form-input id="telefon" type="number"
:state="validanTelefon"
v-model="forma.telefon"></b-form-input>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col sm="3">
                        <label>Ime i prezime</label>
                    </b-col>
                    <b-col sm="9">
                        <b-form-input id="ime_prezime" 
:state="validnoIme"
v-model="forma.ime_prezime"></b-form-input>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col sm="3">
                        <label for="spisakJela">Jela:</label>
                    </b-col>
                    <b-col sm="3">
                        <select class="form-select" id="spisakJela">
                        <option></option>
                        </select>
                    </b-col>
                </b-row>
                <b-row>
                    <b-col sm="3">
                        <label for="kolicina">Kolicina:</label>
                    </b-col>
                    <b-col sm="1">
                        <b-form-input type="number" min="0" step="1" required id="kolicina" name="kolicina" class="form-control"
                             style="max-width: 100px;"></b-form-input>
                    </b-col>
                    <b-col sm="1">
                        <b-button class="btn btn-success" type="button" @click="dodajUSpisak">+</b-button>
                    </b-col>
                </b-row>
            </b-container>
            <div id="stavka">

            </div>      
            <b-button @click="posalji()" variant="primary">Posalji</b-button>
            <input type="hidden" name="jela" id="jela-input">
        </div>
    </div>
  
  </template>
  
  <style>
  @import url('https://fonts.googleapis.com/css2?family=Gloock&display=swap');
  
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }
  
  nav {
    padding: 30px;
  }
  
  nav a {
    font-weight: bold;
    color: #2c3e50;
  }
  
  nav a.router-link-exact-active {
    color: #42b983;
  }
  
  .center {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
  </style>
  
  <script>
  import Header from '../components/Header.vue'
  import JeloList from '../components/JeloList.vue'
  import { mapActions, mapState } from 'vuex';
  
  
  export default {
    name: 'App',
    components: {
      Header, JeloList,
    },
    data(){
        return {
            headerTitle: "Narudzbina",
            statusnaPoruka: null,
            statusnaPorukaTip: null,
            forma: {
                adresa: null,
                telefon: null,
                ime_prezime: null
             }

        }
       
    },
    computed: {
        ...mapState(['token']),
          validnoIme() {
              if (this.forma.ime_prezime == null) return null;
              else if (this.forma.ime_prezime.length > 2) return true
              else return false;
          },
          validanTelefon() {
              if (this.forma.telefon == null) return null;
              else if (this.forma.telefon.length > 9) return true
              else return false;
          },
          validnaAdresa() {
              if (this.forma.adresa == null) return null;
              else if (this.forma.adresa.length > 2) return true
              else return false;
          },
          
      },
      
    methods: {
          dodajJelo(id, kolicina) {
              document.getElementById("spisakJela").selectedIndex = 0;
              console.log("Select Dropdown Options:", document.getElementById("spisakJela").innerHTML);
              var naziv = document.querySelector(`#spisakJela > option[value='${id}']`).innerHTML;
              var span = document.createElement("span");
              span.classList.add("badge");
              span.classList.add("bg-secondary");
              span.dataset.id = id;
              span.dataset.kolicina = kolicina;
              span.innerHTML = naziv + ' ' + kolicina;
              console.log("Kliknuo si +");
              var button = document.createElement("button");
              button.type = "button";
              button.classList.add("btn");
              button.classList.add("btn-default");
              button.classList.add("btn-sm");
              button.innerHTML = "X";

              span.appendChild(button);
              document.getElementById("stavka").appendChild(span);
              document.getElementById("stavka").appendChild(document.createTextNode(" "));
              button.addEventListener("click", function () {
                  var id = this.parentNode.dataset.id;
                  this.parentNode.parentNode.removeChild(this.parentNode);
                  document.querySelector(`#spisakJela > option[value='${id}']`).disabled = false;

                  var spanovi = document.querySelectorAll("#stavka > span.badge");
                  var niz = Array.from(spanovi).map(span => ({
                      id: span.dataset.id,
                      kolicina: span.dataset.kolicina
                  }));
                  var nizJSON = JSON.stringify(niz);
                  console.log(nizJSON);
                  document.getElementById('jela-input').value = nizJSON;
              });
              var spanovi = document.querySelectorAll("#stavka > span.badge");
              var niz = [];
              for (let i = 0; i < spanovi.length; i++) {
                  niz.push(spanovi[i].dataset.id);
              }
              var nizJSON = JSON.stringify(niz);
              document.getElementById('jela-input').value = nizJSON;
          },
          dodajUSpisak() {
              var id = document.getElementById("spisakJela").value;
              var kolicina = document.getElementById("kolicina").value;
              if (!id) {
                  alert("Izaberi sastojak");
                  return;
              }
              this.dodajJelo(id, kolicina);
          },


          posalji() {
              if (this.validnoIme && this.validanTelefon && this.validnaAdresa) {
                  console.log(JSON.stringify(this.forma))
                  var novaNarudzbina = {};
                  novaNarudzbina.status = 'Novo';
                  var date = new Date();
                  novaNarudzbina.vreme_narucivanja = date;
                  novaNarudzbina.ime_prezime = this.forma.ime_prezime;
                  novaNarudzbina.telefon = this.forma.telefon;
                  novaNarudzbina.adresa = this.forma.adresa;
                  var jelaIds = [];
                  var komade = [];
                  var stavke = [];
                  Array.from(document.querySelectorAll("#stavka > span.badge")).forEach(span => {
                      var stavka = {
                          jelo_id: span.dataset.id,
                          komada: span.dataset.kolicina
                      };

                      stavke.push(stavka);
                  });
                  console.log(stavke);
                  novaNarudzbina.stavke = stavke;
                  fetch('http://localhost:9000/narudzbina/nova-narudzbina', {
                      headers: {
                          'Content-Type': 'application/json',
                          'Authorization': `Bearer ${this.token}`,
                      },
                      method: "POST",
                      body: JSON.stringify(novaNarudzbina)

                  })
                      .then(res => res.json())
                      .then(res => {
                          console.log(res);
                          if (res.error) {
                              this.statusnaPoruka = res.error;
                              this.statusnaPorukaTip = 'danger';
                          } else {
                              this.statusnaPoruka = "Narudzbina je uspesno poslata";
                              this.statusnaPorukaTip = 'success';
                          }
                      });
              }
              else {
                  return;
              }
          }

      },
    mounted() {
        console.log(this.token);
        fetch("http://localhost:9000/jelo/", {
        method: 'GET',
           headers: {
            'Authorization': `Bearer ${this.token}`,
                'Content-Type': 'application/json' 
            },
            })
        .then(resp => resp.json())
        .then(data => {
          for (let i = 0; i < data.length; i++) {
            let opcija = document.createElement("option");
            opcija.value = data[i].id;
            opcija.innerHTML = data[i].naziv;
            
            document.getElementById("spisakJela").appendChild(opcija);
          }

        })
        .catch(err => console.log(err));
    },
  
  
  
  }
  
  
  </script>

  <style>
</style>
  